<template>
  <div class="vue_viewer" ref="viewer">
    <template v-if="$slots.default">
      <slot></slot>
    </template>
    <template v-else>
      <div class="vue_viewer_item" v-for="(item, index) in list" :key="index">
        <img v-if="(item instanceof Object)" :src="item.url" :alt="item.title">
        <img v-else :src="item">
      </div>
    </template>
  </div>
</template>

<script type="text/ecmascript-6">
import Viewer from 'viewerjs'
import 'viewerjs/dist/viewer.css'

const TOOLBAR_OPTIONS = {    // {key: Boolean | Number | String | Function}{key: {show: Boolean | Number, size: String, click: Function }} sizes: "small", "medium" (default) and "large"
  zoomIn: 1,
  zoomOut: 1,
  oneToOne: 1,
  reset: 1,
  prev: 1,
  play: 1,
  next: 1,
  rotateLeft: 1,
  rotateRight: 1,
  flipHorizontal: 1,
  flipVertical: 1
}

export default {
  name: 'VueViewer',
  props: {
    images: {
      type: [String, Object, Array]
    },
    visible: {
      type: Boolean,
      default: false
    },
    inline: {                // 
      type: Boolean,
      default: false
    },
    button: {                // 
      type: Boolean,
      default: true
    },
    navbar: {                //  0/false1/true2768399241200
      type: [Boolean, Number],
      default: 1
    },
    title: {                 //  0/false1/true2768399241200, Function: [Number, Function] Function(image, imageData)
      type: [Boolean, Number, Function, Array],
      default: 1
    },
    toolbarType: {               //  0/false1/true2768399241200
      type: [Boolean, Number],
      default: 1
    },
    toolbarOptions: {        // 
      type: Object
    },
    tooltipShow: {               // 
      type: Boolean,
      default: true
    },
    movable: {               // 
      type: Boolean,
      default: true
    },
    zoomable: {              // 
      type: Boolean,
      default: true
    },
    rotatable: {             // 
      type: Boolean,
      default: true
    },
    scalable: {              // 
      type: Boolean,
      default: true
    },
    transition: {            // transition
      type: Boolean,
      default: true
    },
    fullscreen: {            // 
      type: Boolean,
      default: true
    },
    keyboard: {              // 
      type: Boolean,
      default: true
    },
    backdrop:{               // static
      type: [Boolean, String],
      default: true
    },
    loading: {               // 
      type: Boolean,
      default: true
    },
    loop: {                  // 
      type: Boolean,
      default: true
    },
    interval: {              // 
      type: Number,
      default: 5000
    },
    minWidth: {              // 
      type: Number,
      default: 200
    },
    minHeight: {             // 
      type: Number,
      default: 200
    },
    zoomRatio: {             // 
      type: Number,
      default: 0.1
    },
    minZoomRatio: {          // 
      type: Number,
      default: 0.01
    },
    maxZoomRatio: {          // 
      type: Number,
      default: 100
    },
    zIndex: {                // z-index
      type: Number,
      default: 9999
    },
    zIndexInline: {          // z-index
      type: Number,
      default: 100
    },
    url: {                   // 
      type: [String, Function],
      default: 'src'
    },
    container: {             // 
      type: [Element, String],
      Default: 'body'
    },
    filter: {                // 
      type: Function
    },
    toggleOnDblclick: {      // 
      type: Boolean,
      default: true
    },
    value: {
      type: Number
    }
  },
  data () {
    return {
      list: [],
      index: 0,
      toolbar: '',
      closed: false
    }
  },
  watch: {
    images () {
      this.$nextTick(() => {
        this.update()
      })
    },
    visible (newVal) {
      if (this.closed === newVal) return
      this.closed = newVal
      if (newVal) {
        this.show()
      } else {
        this.hide()
      }
    },
    index () {
      this.$emit('input', this.index)
    },
    value () {
      if (!this.value && this.value !== 0) return
      this.view(this.value)
    }
  },
  created () {
    this._optionsInit()
    this.$nextTick(() => {
      this._viewerInit()
    })
  },
  beforeDestroy () {
    this.destroy()
  },
  methods: {
    _optionsInit () {
      if (this.images instanceof Array) {
        this.list = this.images
      } else {
        this.list = [this.images]
      }
      this.index = this.value
      if (this.toolbarOptions) {
        this.toolbar = Object.assign(TOOLBAR_OPTIONS, this.toolbarOptions);
      } else {
        this.toolbar = this.toolbarType
      }
      this.closed = this.visible
    },
    _viewerInit () {
      this.viewer = new Viewer(this.$refs.viewer, {
        initialViewIndex: this.index,
        inline: this.inline,
        button: this.button,
        navbar: this.navbar,
        title: this.title,
        toolbar: this.toolbar,
        tooltip: this.tooltipShow,
        movable: this.movable,
        zoomable: this.zoomable,
        rotatable: this.rotatable,
        scalable: this.scalable,
        transition: this.transition,
        fullscreen: this.fullscreen,
        keyboard: this.keyboard,
        backdrop: this.backdrop,
        loading: this.loading,
        loop: this.loop,
        interval: this.interval,
        minWidth: this.minWidth,
        minHeight: this.minHeight,
        zoomRatio: this.zoomRatio,
        minZoomRatio: this.minZoomRatio,
        maxZoomRatio: this.maxZoomRatio,
        zIndex: this.zIndex,
        zIndexInline: this.zIndexInline,
        url: this.url,
        container: this.container,
        filter: this.filter,
        toggleOnDblclick: this.toggleOnDblclick,
        ready: (event) => {                 // ready
          this.$emit('ready', event)
        },
        show: (event) => {                  // -
          this.$emit('show', event)
        },
        shown: (event) => {                 // -
          this.closed = true
          this.$emit('shown', event)
          this.$emit('update:visible', true)
        },
        hide: (event) => {                  // -
          this.$emit('hide', event)
        },
        hidden: (event) => {                // -
          this.closed = false
          this.$emit('hidden', event)
          this.$emit('update:visible', false)
        },
        view: (event) => {                  // -
          this.$emit('view', event)
        },
        viewed: (event) => {                // -
          this.index = event.detail.index
          this.$emit('viewed', event)
        },
        zoom: (event) => {                  // -
          this.$emit('zoom', event)
        },
        zoomed: (event) => {                // -
          this.$emit('zoomed', event)
        }
      })
      if (this.visible) {
        this.show()
      }
    },
    //  immediate = 
    show (immediate) {
      this.viewer.show(immediate)
      return this
    },
    //  immediate = 
    hide (immediate) {
      this.viewer.hide(immediate)
      return this
    },
    // index = 
    view (index) {
      this.viewer.view(index)
      return this
    },
    //  loop = 
    prev (loop) {
      this.viewer.prev(loop)
      return this
    },
    //  loop = 
    next (loop) {
      this.viewer.next(loop)
      return this
    },
    //  offsetX = 'px', offsetX = 'px offsetX'
    move (offsetX, offsetY) {
      this.viewer.move(offsetX, offsetY)
      return this
    },
    //  x = 'px', y = 'px x'
    moveTo (x, y) {
      this.viewer.moveTo(x, x)
      return this
    },
    //  ratio = '' hasTooltip = ''
    zoom (ratio, hasTooltip) {
      this.viewer.zoom(ratio, hasTooltip)
      return this
    },
    //  ratio = '' hasTooltip = ''
    zoomTo (ratio, hasTooltip) {
      this.viewer.zoomTo(ratio, hasTooltip)
      return this
    },
    //  ratio = ''
    rotate (degree) {
      this.viewer.rotate(degree)
      return this
    },
    //  ratio = ''
    rotateTo (degree) {
      this.viewer.rotateTo(degree)
      return this
    },
    //  scaleX = '', scaleY = ' scaleX'
    scale (scaleX, scaleY) {
      this.viewer.scale(scaleX, scaleY)
      return this
    },
    //  scaleX = ''
    scaleX (scaleX) {
      this.viewer.scaleX(scaleX)
      return this
    },
    //  scaleY = ''
    scaleY (scaleY) {
      this.viewer.scaleY(scaleY)
      return this
    },
    //  fullscreen = ''
    play (fullscreen) {
      this.viewer.play(fullscreen)
      return this
    },
    // 
    stop () {
      this.viewer.stop()
      return this
    },
    // 
    full () {
      this.viewer.full()
      return this
    },
    // 
    exit () {
      this.viewer.exit()
      return this
    },
    // 
    tooltip () {
      this.viewer.tooltip()
      return this
    },
    // 
    toggle () {
      this.viewer.toggle()
      return this
    },
    // 
    reset () {
      this.viewer.reset()
      return this
    },
    // 
    update () {
      this.viewer.update()
      return this
    },
    // 
    destroy () {
      this.viewer.destroy()
    }
  }
}
</script>

<style lang="scss">
.vue_viewer_item{
  display: inline-block;
  margin: 5px;
  img{
    width: 100px;
    height: 100px;
    vertical-align: top;
    object-fit: cover;
  }
}
</style>



